@import "../../variables/font.scss";
@import "../../variables/variables.scss";

.contact-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 15px 0 60px;
  &__content {
    max-width: 920px;
    width: 100%;
    display: flex;
    justify-content: space-between;

    @media screen and(max-width: $tablet-width) {
      flex-direction: column;
      align-items: center;
      gap: 30px;
    }
  }
  &__contact-form-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 3px solid $light-blue;
    max-width: 445px;
    width: 100%;
    padding: 20px 62px 40px;

    @media screen and(max-width: $tablet-width) {
      width: 325px;
      height: 400px;

      padding: 35px 50px 40px;
    }
  }
  &__title {
    width: 100%;
    font-size: 25px;
    text-align: center;
    margin-bottom: 5px;
    @media screen and(max-width: $tablet-width) {
      font-size: 15px;
    }
  }
