  &__title-cards {
    width: 100%;
    font-size: 25px;
    text-align: center;

    @media screen and(max-width:$tablet-width) {
      font-size: 15px;
    }
  }
  &__cards {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;
  }
  &__card {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    font-size: 18px;
    border: 2px solid $light-blue;
    height: 215px;
    @media screen and(max-width:$tablet-width) {
      font-size: 15px;
      height: 140px;
    }
  }
  &__card-contacts {
    display: flex;
    margin-top: 20px;
    gap: 10px;
    @media screen and(max-width:$tablet-width) {
      margin: 0;
      img {
        width: 20px;
        height: 20px;
      }
    }
  }
  &__card-image {
    margin-top: 5px;
    @media screen and(max-width:$tablet-width) {
      margin-top: 15px;
      width: 25px;
      height: 25px;
    }
  }
}
